.App{
    text-align: center;
}
.card{
    position: relative;
    width: 70%;
    min-height: 70%;
    margin: auto;
    margin-bottom: 10px;
    margin-top: 1%;
    background: -webkit-linear-gradient(45deg ,rgb(9, 26, 99) 30%, rgb(59,87,188) 90%);
    background: -o-linear-gradient(45deg ,rgb(9, 26, 99) 30%, rgb(59,87,188) 90%);
    background: linear-gradient(45deg ,rgb(9, 26, 99) 30%, rgb(59,87,188) 90%);
    background-size: cover;
    -webkit-box-shadow: 5px 10px 5px 10px rgba(110, 144, 199, 0.8);
            box-shadow: 5px 10px 5px 10px rgba(110, 144, 199, 0.8);
    padding: 20px;
}
.title{
    font-size: 3rem;
    letter-spacing: .2rem;
    color: #f1f1f1;
}
.search input{
    width: 80%;
    padding: 10px;
    border-color: rgb(59,87,188);
    outline: none;
}
.btn-center{
    padding: 9px 16px;
    font-size: .8rem;
    background: -webkit-linear-gradient(45deg ,rgb(9, 26, 99) 30%, rgb(59,87,188) 90%);
    background: -o-linear-gradient(45deg ,rgb(9, 26, 99) 30%, rgb(59,87,188) 90%);
    background: linear-gradient(45deg ,rgb(9, 26, 99) 30%, rgb(59,87,188) 90%);
    background-size:contain;
    color: #fff;
    cursor: pointer;
}
.btn-center:hover{
    background: -webkit-linear-gradient(45deg ,rgb(108, 123, 192) 30%,rgb(9,26,99)90%);
    background: -o-linear-gradient(45deg ,rgb(108, 123, 192) 30%,rgb(9,26,99)90%);
    background: linear-gradient(45deg ,rgb(108, 123, 192) 30%,rgb(9,26,99)90%);

}
.main-container{
    color: #fff;
    font-weight: 300;
}
.main-container h3{
    letter-spacing: .2rem;
}
.weather-icons{
    font-size: 3rem;
    font-weight: 300;
}
.weather-icons{
    -webkit-animation: iconanimation 3s infinite;
            animation: iconanimation 3s infinite;
}
@-webkit-keyframes iconanimation {
    from{
        -webkit-transform: scale(0.5,0.5);
                transform: scale(0.5,0.5);
    }
    to{
        -webkit-transform: scale(1.1,1.1);
                transform: scale(1.1,1.1);
    }
}
@keyframes iconanimation {
    from{
        -webkit-transform: scale(0.5,0.5);
                transform: scale(0.5,0.5);
    }
    to{
        -webkit-transform: scale(1.1,1.1);
                transform: scale(1.1,1.1);
    }
}
.location h5{
    margin-left: 1rem;
    font-size: 1.2rem;
    font-weight: 300;
}
.location-icon{
    -webkit-animation: iconlocation 2s infinite;
            animation: iconlocation 2s infinite;
    font-size: 1rem;
}
@-webkit-keyframes iconlocation {
    0%{
        -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
    }
    50%{
        -webkit-transform: rotateY(180deg);
                transform: rotateY(180deg);
    }
    100%{
        -webkit-transform: rotateY(360deg);
                transform: rotateY(360deg);
    }
}
@keyframes iconlocation {
    0%{
        -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
    }
    50%{
        -webkit-transform: rotateY(180deg);
                transform: rotateY(180deg);
    }
    100%{
        -webkit-transform: rotateY(360deg);
                transform: rotateY(360deg);
    }
}
@media  screen and (max-width:768px){
    .search input{
        width: 50%;
    }
    .btn-center{
        margin: .5rem;
    }
    .main-container h3{
        font-size: .8rem;
    }
    

}
@media screen and (max-width:320px){
    .search input{
        width: 90%;
    }
}


